* {
  box-sizing: border-box;
}

/* GLOBAL STYLES */

body,
.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
  margin: 0;
  background: url(https://www.toptal.com/designers/subtlepatterns/patterns/lightpaperfibers.png);
}
.header{
  height: 50px;
  width: 100vw;
  display: block;
  background-color: #fc6103;
  color: white;
  font-size: 22px;
  line-height: 45px;
  font-weight: bold;
  padding: 0 10px;
  text-align: center;
}

button {
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  color: #fc6103;
  font-weight: bold;
  background: white;
  padding: 4px 8px;
  border-radius: 12px;
}
button::selection{
  border: none;
}
.b1{
  background-color: #fc6103;
  color: white;
  padding: 0 16px;
  border-radius: 20px;
  box-shadow: 0 0 10px -4px black;
}
button:hover {
  color: black;
}

.App {
  display: flex;
}

/* SIDEBAR STYLES */

.app-sidebar {
  width: 30%;
  height: calc(100vh-40px);
  border-right: 1px solid #ddd;
}

.app-sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 25px;
}
.app-sidebar-header h1 {
  margin: 0;
}

.app-sidebar-notes {
  height: calc(100vh - 78px);
  overflow-y: scroll;
}
.app-sidebar-note {
  padding: 12px;
  cursor: pointer;
}
.sidebar-note-title {
  display: flex;
  justify-content: space-between;
}
.app-sidebar-note button {
  color: crimson;
}
.app-sidebar-note p {
  margin: 10px 0;
}
.app-sidebar-note small {
  display: block;
  color: #999;
}

.app-sidebar-note:hover {
  background: #ddd;
}
.app-sidebar-note.active,
.app-sidebar-note.active small {
  background: #c5c5c5;
  color: black;
}

/* NOTE EDITOR/PREVIEW (MAIN) STYLES */

.app-main {
  width: 70%;
  height: 100vh;
}
.app-main-note-edit,
.app-main-note-preview {
  height: 50vh;
}

.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #999;
}

/* Editing */
.app-main-note-edit {
  padding: 24px;
}

.app-main-note-edit input, textarea{
  display: block;
  border: 2px solid #ddd;
  margin-bottom: 20px;
  width: 100%;
  height: calc(100vh - 130px);
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}
.app-main-note-edit input {
  height: 50px;
  font-size: 2rem;
  border: none;
  border-bottom: 3px solid #fc6103;
  color: rgb(82, 82, 82);
}
input:focus{
  outline: none;
}